














































































































































































import { Vue, Component } from "vue-property-decorator";

@Component
export default class PublicHeader extends Vue {
  // data
  disturbs = ["For One Hour", "Until Tomorrow", "Forever", "Turn Off"];
  consoledialog = false;
  logindialog = false;
  consoleBtn = ["red", "orange", "green"];

  //computed
  get bookMarkLabel(): string {
    return this.$store.state.bookMarkStatus ? "BookMark" : "Feed";
  }
  set bookMarkStatus(value: boolean) {
    this.$store.commit("switchBookMarkStatus", value);
  }

  // method
  directToHome(): void {
    document.location.href = "/";
  }

  switchDisturbStatus(mode: string) {
    //TODO: 可以合併成 switcher?
    this.$store.commit("switchDisturbStatus", mode);
  }

  switchLayoutStatus() {
    this.$store.commit("switchLayoutPanelStatus");
  }
}
